@mixin mobile {
  @media screen and (max-width: 480px) {
    @content;
  }
}
@mixin tablet {
  @media screen and (max-width: 768px) {
    @content;
  }
}
@mixin laptop {
  @media screen and (max-width: 1200px) {
    @content;
  }
}
:root {
  --Green_01: rgba(190, 246, 227, 0.864); /* Define a global variable */
  --logo_c: rgb(20, 152, 108);
  --button: rgba(20, 152, 108, 0.555);
  --chatInfoBar: #daf0d5b3;
  --msgBg: #ddf7e7;
  --msgTab: rgb(154, 181, 148);
  --sidebarBg:rgba(218, 240, 213, 0.702);
  --navbarBg: rgb(210, 237, 204);
}

.formContainer {
  // background-color: var(--Green_01);
  background-image: linear-gradient(#ddf7e7, #90f07bb3);
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .formWrapper {
    background-color: white;
    padding: 20px 60px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;

    .logo {
      color: var(--logo_c);
      font-weight: bold;
      font-size: 24px;

    }

    .title {
      color: var(--logo_c);
      font-size: 12px;
    }

    form {
      display: flex;
      flex-direction: column;
      gap: 15px;

      input {
        padding: 15px;
        border: none;
        width: 250px;
        border-bottom: 1px solid var(--button);
        &::placeholder {
          color: rgb(175, 175, 175);
        }
      }

      button {
        background-color: var(--button);
        color: white;
        padding: 10px;
        font-weight: bold;
        border: none;
        cursor: pointer;
        
      }

      label {
        display: flex;
        align-items: center;
        gap: 10px;
        color: var(--button);
        font-size: 12px;
        cursor: pointer;

        img {
          width: 32px;
        }
      }
    }
    p {
      color: var(--logo_c);
      font-size: 12px;
      margin-top: 10px;
    }
  }
}

.home {
  // background-color: var(--Green_01);
  background-image: linear-gradient(#ddf7e7, #90f07bb3);
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .container {
    border: 1px solid white;
    border-radius: 10px;
    width: 65%;
    height: 80%;
    display: flex;
    overflow: hidden;
    @include tablet {
     width: 90%;
    }

    .sidebar {
      flex: 1;
      background-color: var(--sidebarBg);
      position: relative;

      .navbar {
        display: flex;
        align-items: center;
        background-color: var(--navbarBg);
        height: 50px;
        padding: 10px;
        justify-content: space-between;
        color: var(--button);

        .logo {
          font-weight: bold;
          color: var(--logo_c);
          @include tablet {
            display: none;
          }
        }

        .user {
          display: flex;
          gap: 10px;

          img {
            background-color: #ddddf7;
            height: 24px;
            width: 24px;
            border-radius: 50%;
            object-fit: cover;
          }

          button {
            background-color: var(--button);
            
            color: white;
            font-size: 12px;
            border-radius: 15%;
            border: none;
            cursor: pointer;
            @include tablet {
              position: absolute;
              bottom: 10px;
            }
          }
        }
      }
      .search {
        border-bottom: 1px solid var(--button);

        .searchForm {
          padding: 10px;

          input {
            background-color: transparent;
            border: none;
            color: var(--button);
            outline: none;

            &::placeholder {
              color: lightgray;
            }
          }
        }
      }

      .userChat {
        padding: 10px;
        display: flex;
        align-items: center;
        gap: 10px;
        color: var(--button);
        cursor: pointer;

        &:hover {
          background-color: rgb(202, 241, 195);
        }

        img {
          width: 50px;
          height: 50px;
          border-radius: 50%;
          object-fit: cover;
        }

        .userChatInfo {
          span {
            font-size: 18px;
            font-weight: 500;
          }
          p {
            font-size: 14px;
            color: lightgray;
          }
        }
      }
    }
    .chat {
      flex: 2;

      .chatInfo {
        height: 50px;
        background-color: var(--chatInfoBar);
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
        color: var(--button);
        font-weight: 600;
      }

      .chatIcons {
        display: flex;
        gap: 10px;

        img {
          height: 24px;
          cursor: pointer;
        }
      }

      .messages {
        background-color: var(--msgBg);
        padding: 10px;
        width: 100%;
        height: calc(100% - 160px);
        overflow: scroll;

        .message {
          display: flex;
          gap: 20px;
          margin-bottom: 20px;

          .messageInfo {
            display: flex;
            flex-direction: column;
            color: gray;
            font-weight: 300;

            img {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              object-fit: cover;
            }
          }
          .messageContent {
            max-width: 80%;
            display: flex;
            flex-direction: column;
            gap: 10px;

            p {
              background-color: white;
              padding: 10px 20px;
              border-radius: 0px 10px 10px 10px;
              max-width: max-content;
            }

            img {
              width: 50%;
            }
          }

          &.owner {
            flex-direction: row-reverse;

            .messageContent {
              align-items: flex-end;
              p {
                background-color: var(--msgTab);
                color: white;
                border-radius: 10px 0px 10px 10px;
              }
            }
          }
        }
      }

      .input {
        height: 50px;
        background-color: white;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        input {
          width: 100%;
          border: none;
          outline: none;
          color: #2f2d52;
          font-size: 18px;

          &::placeholder {
            color: lightgray;
          }
        }

        .send {
          display: flex;
          align-items: center;
          gap: 10px;

          img {
            height: 24px;
            cursor: pointer;
          }

          button {
            border: none;
            padding: 10px 15px;
            color: white;
            border-radius: 15%;
            background-color: var(--button);
            cursor: pointer;
          }
        }
      }
    }
  }
}
